* {
  box-sizing: border-box;
}

body {
  color: #111;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  font-size: 18px;
  font-style: normal;
  font-weight: 200;
  line-height: 1.5;
}

.breed-select {
  max-width: 400px;
  margin: auto auto 30px;
}

img {
  border: 1px solid #0d161b;
  display: block;
}

.loader {
  width: 250px;
  width: 100px;
  height: 100px;
  justify-content: space-between;
  margin: 20% auto;
  font-size: 0;
  display: flex;
  position: relative;
}

.loader:before, .loader:after {
  content: "";
  border-radius: 50%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.loader:after {
  animation: rotate 2s linear infinite;
  box-shadow: inset 0 2px #ff3d00;
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.cat-info {
  display: flex;
}

.cat-info-text {
  padding: 10px;
  display: block;
}

.cat-title {
  margin: 0;
}

.temperament {
  font-weight: bold;
}

.hidden {
  display: none;
}

